// Mail
document.addEventListener("sendEmail", () => {
  //         
  $("#form_calback").on("submit", function (event) {
    event.preventDefault();

    let form = $("#formCallback");
    let button = $("#btn-submit");
    let answer = $("#answer");
    let loader = $("#loader");

    $.ajax({
      url: "/vendor/handler.php",
      type: "POST",
      data: form.serialize(),
      beforeSend: function () {
        answer.empty();
        button.attr("disabled", true).css("margin-bottom", "20px");
        loader.fadeIn();
      },
      success: function (result) {
        loader.fadeOut(300, function () {
          answer.text(result);
        });
        form.find(".field").val("");
        button.attr("disabled", false);
      },
      error: function () {
        loader.fadeOut(300, function () {
          answer.text(" !  .");
        });
        button.attr("disabled", false);
      },
    });
  });
});

